/* -----------------
    Global Styles
-----------------  */
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap");
:root {
  --ff-primary: "Roboto", sans-serif;
  --ff-secondary: "TrebuchetMS";
  --primary: #b41446;
  /* --secondary : #00aebc; */
  --secondary: #009997;
  --white: #fff;
  --black: #000;
  --white-40: rgba(255, 255, 255, 0.4);
  --white-20: rgba(255, 255, 255, 0.2);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(209, 22%, 49%);
  --clr-grey-6: #829ab0;
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

body {
  /* min-height: 100vh; */
  height: 100%;
  background: url("images/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: var(--ff-secondary);
  line-height: 1.5;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1::selection,
h2::selection,
h3::selection,
h4::selection,
h5::selection,
h6::selection,
p::selection,
li::selection,
span::selection {
  background: #cceff2;
}
h1::-moz-selection,
h2::-moz-selection,
h3::-moz-selection,
h4::-moz-selection,
h5::-moz-selection,
h6::-moz-selection,
p::-moz-selection,
li::-moz-selection,
span::-moz-selection {
  background: #cceff2;
}
h2 {
  font-size: 1.75rem;
}
@media screen and (max-width: 600px) {
  h2 {
    font-size: 1.5rem;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

/*------------------
    Custom Scrollbar
 ------------------*/

::-webkit-scrollbar {
  width: 7px;
}
::-webkit-scrollbar-track {
  background-color: var(--white);
}
::-webkit-scrollbar-thumb {
  background-color: var(--secondary);
}

/*-----------
    Navbar
 -----------*/

nav {
  margin-top: 3rem;
}
.nav-toggle {
  display: none;
}
.nav-links {
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 800px) {
  nav {
    margin-top: 1rem;
    position: relative;
  }
  .nav-links {
    display: none;
  }
  .nav-toggle {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 2rem;
  }
  .nav-toggle :not(:last-child) {
    margin-bottom: 1rem;
  }
  .nav-toggle ul {
    padding-right: 1rem;
  }
  .nav-toggle-btn {
    margin-left: 0;
    /* text-align: center; */
    font-weight: bold;
    width: 3rem;
    height: 3rem;
  }
  .nav-toggle .regular-btn {
    margin: 1rem;
  }
}

/*----------------
    Navbar Buttons
 ----------------*/
.btn {
  display: inline-block;
  position: relative;
  border: none;
  outline: none;
  color: var(--primary);
  background: var(--white-20);
  cursor: pointer;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  padding: 0.75rem 1rem;
  border-radius: 1.5rem;
  font-size: 1rem;
  font-family: var(--ff-primary);
  text-transform: capitalize;
  user-select: none;
  margin-left: 1rem;
  backdrop-filter: blur(5px);
}
.btn:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 1.5rem;
  z-index: -1;
  opacity: 0;
  filter: blur(3px);
  background: radial-gradient(#99dfe4, rgba(255, 255, 255, 0.1));
  transition: opacity 0.3s ease-in-out;
}
.btn:hover {
  background: var(--white-40);
  color: var(--secondary);
}
.btn:hover:before {
  animation: animate 0.5s linear infinite;
  -webkit-animation: animate 0.5s linear infinite;
}
.btn:active {
  border-top: 2px solid rgba(0, 0, 0, 0.2);
  border-left: 2px solid rgba(0, 0, 0, 0.2);
  border-bottom: 2px solid var(--white-40);
  border-right: 2px solid var(--white-40);
  box-shadow: none;
}
.btn:active:before {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  filter: blur(3px);
}
.active-btn {
  display: inline-block;
  position: relative;
  border: none;
  outline: none;
  color: #008b96;
  background: radial-gradient(rgba(204, 235, 234, 1), rgba(204, 235, 234, 0.2));
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  padding: 0.75rem 1rem;
  border-radius: 1.5rem;
  font-size: 1rem;
  font-family: var(--ff-primary);
  text-transform: capitalize;
  user-select: none;
  margin-left: 1rem;
  backdrop-filter: blur(5px);
  pointer-events: none;
}

@keyframes animate {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.5;
  }
}
@-webkit-keyframes animate {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.5;
  }
}
/*----------------
    Regular Button
 ----------------*/
.regular-btn {
  display: inline-block;
  position: relative;
  border: none;
  outline: none;
  color: var(--primary);
  background: var(--white-20);
  cursor: pointer;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(5px);
  padding: 0.25rem 0.5rem;
  border-radius: 1.5rem;
  font-size: 1rem;
  text-transform: capitalize;
  margin-top: 2rem;
  margin-left: 2rem;
  overflow: hidden;
  transition: transform 0.1s ease;
}
.regular-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    rgba(153, 223, 228, 1),
    rgba(204, 235, 234, 0.5),
    rgba(255, 255, 255, 0.1)
  );
  opacity: 0;
  z-index: -1;
}
.regular-btn:hover,
.regular-btn:hover .testimonial-btn-icons {
  color: var(--secondary);
}
.regular-btn:hover::before {
  opacity: 1;
}
.regular-btn:active {
  border-top: 2px solid rgba(0, 0, 0, 0.2);
  border-left: 2px solid rgba(0, 0, 0, 0.2);
  border-bottom: 2px solid var(--white-20);
  border-right: 2px solid var(--white-20);
  box-shadow: none;
  color: var(--secondary);
}
.regular-btn:active::before {
  opacity: 1;
}

/*-----------
    Home
 -----------*/

section {
  background-color: var(--white-20);
  box-shadow: 10px 20px 30px rgba(0, 0, 0, 0.3);
  border-top: 3px solid var(--white-40);
  border-left: 3px solid var(--white-40);
  margin: 2rem auto 4rem auto;
  border-radius: 2rem;
  backdrop-filter: blur(5px);
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}
.name-box {
  width: 100%;
  font-weight: 300;
  text-align: center;
  margin: 3rem auto;
}
.name-box h1 {
  font-family: var(--ff-primary);
  font-size: 3rem;
  letter-spacing: 0.3rem;
  margin-bottom: 2rem;
}
.name-box h1 span {
  color: var(--primary);
}
.name-box h2 {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
  font-family: var(--ff-primary);
}
.iam {
  margin-bottom: 1rem;
}
.typewritter {
  overflow: hidden;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.4rem;
  font-weight: bold;
  color: var(--primary);
  animation: typing 7s steps(50, end), blink-caret 0.2s step-end infinite;
}
.typewritter span {
  color: var(--secondary);
}
.anu-title {
  display: none;
}
.anu-title .front-end {
  color: var(--primary);
}
.anu-title .and {
  margin: 1rem 0;
}
.anu-title .ui-ux {
  color: var(--secondary);
}
@keyframes typing {
  0% {
    opacity: 0;
    width: 0;
  }
  20% {
    opacity: 1;
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes blink-caret {
  from {
    border-color: transparent;
  }
  to {
    border-color: transparent;
  }
}
.logo-box {
  width: 20rem;
  margin: 5rem auto 7rem auto;
  position: relative;
}
.logo-box img {
  width: 100%;
}
.home-anu-logo-back {
  animation: hideShow 3s ease-in 5s;
  animation-fill-mode: forwards;
}
.home-anu-logo {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  -moz-animation: showHide 3s ease-in 5s forwards, rotation 5s linear 8s;
  -webkit-animation: showHide 3s ease-in 5s forwards, rotation 5s linear 8s;
  -ms-animation: showHide 3s ease-in 5s forwards, rotation 5s linear 8s;
  -o-animation: showHide 3s ease-in 5s forwards, rotation 5s linear 8s;
  animation: showHide 3s ease-in 5s forwards, rotation 5s linear 8s;
}
@media screen and (max-width: 900px) {
  .name-box h1 {
    font-size: 2.5rem;
  }
  .name-box h2 {
    font-size: 1rem;
    letter-spacing: 0.3rem;
  }
  .logo-box {
    width: 18rem;
  }
}
@media screen and (max-width: 800px) {
  .name-box h1 {
    font-size: 2rem;
  }
  .typewritter {
    display: none;
  }
  .anu-title {
    display: block;
  }
  .home-anu-logo-back {
    animation: hideShow 3s ease-in 2s;
    animation-fill-mode: forwards;
  }
  .home-anu-logo {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    -moz-animation: showHide 3s ease-in forwards 2s, rotation 5s linear 5s;
    -webkit-animation: showHide 3s ease-in forwards 2s, rotation 5s linear 5s;
    -ms-animation: showHide 3s ease-in forwards 2s, rotation 5s linear 5s;
    -o-animation: showHide 3s ease-in forwards 2s, rotation 5s linear 5s;
    animation: showHide 3s ease-in forwards 2s, rotation 5s linear 5s;
  }
}
@media screen and (max-width: 500px) {
  .logo-box {
    width: 15rem;
  }
  .name-box h1 {
    font-size: 1.8rem;
  }
  .name-box h2 {
    font-size: 0.9rem;
    letter-spacing: 0.3rem;
  }
}

