/*-----------
    About
 -----------*/

.about-text {
  margin: 5rem 0 3rem 0;
  text-align: center;
  width: 80%;
}

.title {
  font-family: var(--ff-primary);
  color: var(--clr-grey-3);
  letter-spacing: 0.1rem;
  display: flex;
  margin: 0 auto;
  margin-bottom: 2rem;
}
.title span {
  color: var(--secondary);
  text-transform: capitalize;
}

.title::before,
.title::after {
  content: "";
  flex: 1 1;
  border-style: solid;
  border-width: 3px;
  border-image: linear-gradient(
      45deg,
      rgba(255, 255, 255, 0),
      rgba(255, 255, 255, 1),
      rgba(255, 255, 255, 0)
    )
    1;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
  margin: auto;
}
.title::after {
  margin-left: 2rem;
}
.title::before {
  margin-right: 2rem;
}
@media screen and (max-width: 600px) {
  .title::after {
    margin-left: 1rem;
  }
  .title::before {
    margin-right: 1rem;
  }
}
.about-text p {
  font-family: var(--ff-secondary);
  color: var(--clr-grey-1);
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 1.75;
  padding: 0 2rem;
}
.skills_container {
  text-align: center;
  width: 80%;
}
.skills-box {
  display: grid;
  grid-template-columns: repeat(6, minmax(5rem, auto));
  grid-gap: 3rem;
  justify-content: center;
  margin-top: 3rem;
  margin-bottom: 5rem;
}
.skills {
  width: 6rem;
  height: 6rem;
  border-radius: 20px;
  position: relative;
  perspective: 150rem;
  -webkit-perspective: 150rem;
}

.skills_side {
  width: 6rem;
  height: 6rem;
  border-radius: 20px;
  background-color: var(--white-20);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 20px;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transition: all 1s ease;
}

.skills_side-front {
  padding: 0.5rem;
}

.skills_side-back {
  padding: 0.5rem;
  -ms-transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  -moz-transform: rotateY(180deg);
  -o-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.skills:hover .skills_side-front {
  -ms-transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
  -moz-transform: rotateY(-180deg);
  -o-transform: rotateY(-180deg);
  transform: rotateY(-180deg);
}

.skills:hover .skills_side-back {
  -ms-transform: rotateY(0);
  -webkit-transform: rotateY(0);
  -moz-transform: rotateY(0);
  -o-transform: rotateY(0);
  transform: rotateY(0);
}
