/*-----------
    Services
 -----------*/
.services-container {
  width: 90%;
  margin: 3rem auto;
  padding-top: 3rem;
}
.services-container .title {
  width: 90%;
}
.services-box {
  display: grid;
  grid-template-columns: repeat(4, minmax(10rem, auto));
  grid-column-gap: 3rem;
  grid-row-gap: 7rem;
  justify-content: center;
  padding: 3rem;
  padding-top: 4rem;
}
.services-item {
  /* width: 15rem;
  height: 22rem; */
  border-radius: 20px;
  background-color: var(--white-20);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  text-align: center;
  padding: 1.5rem;
  position: relative;
  cursor: default;
  transition: transform 0.3s ease;
}
.services-item:hover {
  transform: scale(1.1);
}
.services-text {
  margin-top: 5rem;
  margin-bottom: 3rem;
  font-family: var(--ff-secondary);
}
.services-text h3 {
  font-size: 1.5rem;
  font-family: var(--ff-secondary);
  color: var(--primary);
  margin-bottom: 1rem;
}

.services-icon-box {
  width: 7rem;
  height: 7rem;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.3);
  border-bottom: 2px solid var(--white-40);
  border-right: 2px solid var(--white-40);
  padding: 0.5rem;
  position: absolute;
  top: -3rem;
  left: 50%;
  transform: translateX(-50%) rotate(135deg);
}
.services-icon-img {
  padding: 0.5rem;
  width: 100%;
  transform: rotate(-135deg);
}
@media screen and (max-width: 1000px) {
  .services-icon-box {
    width: 6rem;
    height: 6rem;
  }
}
@media screen and (max-width: 900px) {
  .services-box {
    grid-template-columns: repeat(2, minmax(10rem, auto));
    padding: 2rem;
    padding-top: 4rem;
  }
  .services-item {
    padding: 1rem;
  }
  .services-icon-box {
    width: 7rem;
    height: 7rem;
  }
}

@media screen and (max-width: 800px) {
  .services-icon-box {
    width: 6rem;
    height: 6rem;
  }
}
@media screen and (max-width: 600px) {
  .services-box {
    grid-template-columns: repeat(1, minmax(6rem, auto));
    padding: 1rem 3rem;
    padding-top: 4rem;
  }
  .services-icon-box {
    width: 7rem;
    height: 7rem;
  }
}
@media screen and (max-width: 500px) {
  .services-container {
    padding-bottom: 3rem;
  }
  .services-box {
    grid-template-columns: 80%;
  }
  .services-icon-box {
    width: 6rem;
    height: 6rem;
  }
  .services-box {
    padding: 0;
    padding-top: 4rem;
  }
}

/*-------------
  Testimonials
---------------*/
.testimonial-container {
  width: 100%;
  position: relative;
}
.testimonial-box {
  width: 70vw;
  margin: 5rem auto;
  background-color: var(--white-20);
  box-shadow: 10px 20px 30px rgba(0, 0, 0, 0.3);
  border-top: 3px solid var(--white-40);
  border-left: 3px solid var(--white-40);
  border-radius: 2rem;
  backdrop-filter: blur(5px);
  position: relative;
}
.next-btn,
.prev-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.prev-btn {
  left: -7rem;
}
.next-btn {
  right: -5rem;
}
.testimonial-btn-icons {
  color: var(--primary);
  font-size: 2rem;
  font-weight: bold;
  margin: 0 auto;
}
.testimonials__heading {
  width: 100%;
  margin-bottom: 1rem;
}
.testimonial-item {
  padding: 2rem;
}
.testimonial__photo {
  width: 7rem;
  border-radius: 50%;
  margin: 0 auto;
  overflow: hidden;
  backface-visibility: hidden;
  position: relative;
}

.testimonial__photo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.testimonial__text {
  margin: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonial__clientInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.testimonial__clientInfo a {
  text-decoration: none;
}
.testimonial__paragraph p {
  margin-left: 3rem;
  font-size: 1rem;
  color: hsl(209, 28%, 39%);
  font-weight: bold;
  font-family: (--ff-secondary);
}

.testimonial__name {
  margin-top: 1rem;
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--primary);
}
.social-icon {
  font-size: 2rem;
  padding: 0.1rem;
  cursor: pointer;
  color: rgba(180, 20, 70, 0.7);
  background: var(--white-20);
  border: 2px solid var(--white-40);
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}
.testimonial__title {
  text-align: center;
  font-style: italic;
  font-size: 0.7rem;
  margin-bottom: 1rem;
  color: var(--secondary);
}

.testimonial-quote {
  font-size: 5rem;
  color: rgba(0, 0, 0, 0.1);
}
.left-quote {
  position: absolute;
  top: 0;
  left: 2%;
}
.right-quote {
  position: absolute;
  bottom: 0;
  right: 2%;
}

@media screen and (max-width: 900px) {
  .testimonial__text {
    flex-direction: column;
  }
  .testimonial__paragraph p {
    margin-left: 0;
    margin-top: 1rem;
  }
}
@media screen and (max-width: 600px) {
  .testimonial-box {
    width: 80vw;
  }
  .prev-btn,
  .next-btn {
    display: none;
  }
}

/*----------
  Contact
------------*/

.contact-container {
  width: 70%;
  padding: 3rem;
}
.contact-form {
  padding-bottom: 3rem;
  padding-top: 0.25rem;
  display: flex;
  justify-content: space-between;
}
.contact-right-box,
.contact-left-box {
  width: 45%;
}
.contact-item {
  display: flex;
  align-items: flex-start;
  margin: 2rem auto;
}
.contact-item-icon {
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.6);
}
.contact-form .input-control {
  display: block;
  width: 80%;
  border: none;
  margin-left: 1rem;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 0 25px 25px 0;
  padding: 1rem;
  font-size: 1rem;
  font-family: var(--ff-secondary);
  color: var(--primary);
  font-weight: bold;
}
.input-control::placeholder {
  color: var(--clr-grey-3);
  text-transform: capitalize;
  font-weight: 500;
  opacity: 0.8;
}
.contact-btn {
  display: block;
  padding: 0.75rem 1rem;
  background: var(--white-20);
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.3);
  color: var(--primary);
  font-size: 1rem;
  text-transform: capitalize;
  font-family: var(--ff-primary);
  border: none;
  border-left: 3px solid var(--white-40);
  border-top: 3px solid var(--white-40);
  border-radius: 20px;
  cursor: pointer;
  margin: 0 auto;
  transition: transform 0.3s ease;
}
.contact-form textarea.input-control {
  height: 9rem;
  resize: none;
  padding: 1rem;
}
.contact-social-links {
  color: var(--secondary);
  margin: 0 0.5rem;
  font-size: 2rem;
}
.contact-form .input-control:focus {
  border: 2px solid rgba(255, 255, 255, 0.5);
}
.my-contact-items-box {
  display: flex;
  margin-bottom: 3rem;
}
.my-contact-items-box :not(:last-child) {
  margin-right: 2rem;
}
.my-contact-item {
  flex: auto;
  padding: 1rem 0;
  background-color: var(--white-20);
  box-shadow: 1px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 3px solid var(--white-40);
  border-left: 3px solid var(--white-40);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s ease;
}
.my-contact-item h4 {
  color: var(--primary);
  font-style: bold;
}
.my-contact-item-icon {
  font-size: 3rem;
  color: rgba(0, 0, 0, 0.3);
  margin-bottom: 1rem;
}
.my-contact-item:hover {
  transform: scale(1.1);
}
@media screen and (max-width: 900px) {
  .my-contact-item h4 {
    font-size: 0.8rem;
  }
  .my-contact-item-icon {
    font-size: 2rem;
  }
  .contact-form {
    flex-direction: column;
  }
  .contact-right-box,
  .contact-left-box {
    width: 100%;
  }
}
@media screen and (max-width: 700px) {
  .my-contact-items-box {
    flex-direction: column;
    align-items: center;
  }
  .my-contact-items-box :not(:last-child) {
    margin-bottom: 1rem;
    margin-right: 0;
  }
  .my-contact-item {
    width: 100%;
  }
  .my-contact-item h4 {
    font-size: 1rem;
  }
}
@media screen and (max-width: 600px) {
  .contact-container {
    width: 80%;
    padding: 2rem;
  }
  .contact-social-links {
    margin: 0 0.3rem;
    font-size: 1.5rem;
  }
  .contact-form .input-control {
    padding: 0.7rem;
    font-size: 0.9rem;
    width: 90%;
  }
  .contact-item-icon {
    font-size: 2.5rem;
  }
  .contact-item {
    margin: 1.5rem auto;
  }
}

/*----------
  Projects
------------*/

.portfolio-container {
  width: 80%;
  margin-top: 2rem;
}

/*----------
  Error
------------*/
.error-container {
  width: 80%;
  margin: auto;
  text-align: center;
}
.error-container h2 {
  margin-bottom: 2rem;
}
.error-container h2 span {
  color: var(--primary);
}
.error-btn {
  display: inline-block;
}
/*--------------
  Resume - theme
---------------*/
.light-theme {
  background: var(--white);
}
.dark-theme {
  background: var(--black);
}
.dark-theme .resume-title h4 span {
  color: #80d7de;
}
.dark-theme h4,
.dark-theme p,
.dark-theme h5,
.dark-theme h6,
.dark-theme li,
.dark-theme .resume-contact span {
  color: var(--white);
}
.dark-theme .resume-footer {
  /* background: var(--primary); */
  background: #da8aa3;
}
.dark-theme .resume-heading h3,
.dark-theme .resume-summary-heading h3 {
  color: #da8aa3;
}
.dark-theme .resume-heading-icons {
  color: var(--clr-grey-9);
}
.dark-theme .resume-contact .resume-contact-container .resume-contact-icon,
.dark-theme .resume-experience .experience-title h4,
.dark-theme .resume-experience .experience-title h5,
.dark-theme .resume-education .resume-education-container h5,
.dark-theme .resume-skills .resume-skills-container .web-tools h4,
.dark-theme .resume-skills .resume-skills-container .graphic-tools h4 {
  color: #80d7de;
}
.resume-btn {
  width: 10rem;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.resume-theme-button {
  font-size: 1.5rem;
  margin: 0;
}
.resume-download-btn {
  padding: 0.1rem 0.5rem;
}
/*------------
  Resume size
-------------*/
.resume-box {
  background-color: var(--white-20);
  box-shadow: 10px 20px 30px rgba(0, 0, 0, 0.3);
  border-top: 3px solid var(--white-40);
  border-left: 3px solid var(--white-40);
  margin: 3rem auto;
  border-radius: 2rem;
  backdrop-filter: blur(5px);
  width: 60rem;
}
.resume-container {
  margin-bottom: 5rem;
  margin-top: 2rem;
  height: 100%;
  width: 50rem;
  overflow: hidden;
  display: grid;
  grid-template-columns: 17rem 33rem;
  grid-template-rows: repeat(14, auto);
  padding-top: 1rem;
}

.resume-logo {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  padding: 1rem 0;
  position: relative;
}
.resume-title {
  text-transform: uppercase;
  margin: auto;
  padding: 1rem 0;
  text-align: center;
}
.resume-summary {
  grid-row: 2 / 5;
  grid-column: 1 / 3;
  padding: 0 2rem;
}
.resume-skills {
  grid-row: 5 / 6;
  grid-column: 1 / 3;
  padding: 0 2rem;
}
.resume-education {
  grid-row: 11 / 14;
  grid-column: 1 / 4;
  padding: 0 2rem;
}
.resume-experience {
  grid-row: 6 / 11;
  grid-column: 1 / 4;
  padding: 0 2rem 0 1rem;
}
.resume-footer {
  grid-row: 14 / 15;
  grid-column: 1 / 4;
  background: var(--secondary);
}
/*--------------
  Resume heading
---------------*/
.resume-heading {
  display: flex;
}
.resume-heading h3 {
  text-transform: uppercase;
  color: var(--secondary);
  font-family: var(--ff-primary);
  letter-spacing: 2px;
  margin-left: 1rem;
}
.resume-heading::after {
  content: "";
  flex: 1 1;
  border-style: solid;
  border-width: 3px;
  border-color: var(--clr-grey-7);
  margin: auto;
}
.resume-heading::after {
  margin-left: 1rem;
}
.resume-heading-icons {
  font-size: 1.5rem;
  color: var(--clr-grey-3);
}
/*-------------
  Resume logo
--------------*/
.resume-logo img {
  width: 60%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/*------------
  Resume title
-------------*/
.resume-title h4 {
  font-family: var(--ff-primary);
  font-size: 1.2rem;
  letter-spacing: 3px;
}
.resume-title h4 span {
  color: var(--primary);
}
.resume-title p {
  font-family: var(--ff-secondary);
  letter-spacing: 3px;
  margin-top: 0.3rem;
  font-size: 0.7rem;
}
/*--------------
  Resume summary
---------------*/
.resume-summary-heading {
  text-transform: uppercase;
  text-align: center;
  color: var(--secondary);
  font-family: var(--ff-primary);
  letter-spacing: 2px;
  display: flex;
  align-items: center;
}
.resume-summary-heading h3 {
  margin-left: 1rem;
}
.resume-summary-heading::before,
.resume-summary-heading::after {
  content: "";
  flex: 1 1;
  border-style: solid;
  border-width: 3px;
  border-color: var(--clr-grey-7);
  margin: auto;
}
.resume-summary-heading::after {
  margin-left: 1rem;
}
.resume-summary-heading::before {
  margin-right: 1rem;
}
.resume-summary-heading-icons {
  font-size: 1.5rem;
  color: var(--clr-grey-3);
}
.resume-summary ul {
  padding: 0.5rem;
}
.resume-summary li {
  font-size: 0.8rem;
  font-family: var(--ff-secondary);
}
.resume-summary-bullet {
  margin-right: 0.5rem;
}
/*--------------
  Resume skills
---------------*/
.resume-skills-container {
  margin: 1rem;
}
.resume-skills-container li p span {
  color: var(--primary);
  font-family: var(--ff-primary);
}
.resume-skills-container li {
  font-family: var(--ff-secondary);
  font-weight: bold;
  font-size: 0.8rem;
}
.resume-skills-container :not(:last-child) {
  margin-bottom: 0.3rem;
}
/*-----------------
  Resume experience
------------------*/
.experience-responsibilities {
  font-size: 0.8rem;
  padding: 0.5rem 0;
}
.experience-responsibilities li {
  padding-bottom: 0;
}
.resume-experience-container {
  padding: 1rem;
}
.resume-experience-container > :not(:last-child) {
  margin-bottom: 1rem;
}
.resume-experience-container p {
  padding-top: 0.5rem;
}
.resume-experience-container h4 {
  font-family: var(--ff-primary);
  color: var(--primary);
  font-weight: bold;
}
.experience-title {
  display: flex;
  justify-content: space-between;
}
.resume-experience-container p {
  font-family: var(--ff-secondary);
  font-size: 0.75rem;
  font-weight: bold;
}
.experience-title h6 {
  font-family: var(--ff-primary);
  font-size: 0.8rem;
  font-weight: bold;
}
/*----------------
  Resume education
-----------------*/
.resume-education-container {
  padding: 1rem;
  padding-left: 3rem;
  display: flex;
  justify-content: space-between;
}
.resume-education-container li {
  padding-bottom: 1rem;
}
.resume-education-container h5 {
  color: var(--primary);
  font-family: var(--ff-primary);
  font-size: 0.9rem;
}
.resume-education-container p {
  font-family: var(--ff-secondary);
  font-weight: bold;
  font-size: 0.8rem;
}
@media screen and (max-width: 600px) {
  .resume-education-container {
    flex-direction: column;
  }
  .resume-education-container > :not(:last-child) {
    margin-bottom: 1rem;
  }
}
/*-------------
  Resume footer
--------------*/
.resume-footer-container {
  display: flex;
  justify-content: space-around;
  padding: 1rem 0;
}
.resume-footer span {
  margin-left: 0.5rem;
}
.resume-footer-links {
  color: var(--white);
  font-weight: bold;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 900px) {
  .resume-box {
    width: 90%;
    padding: 2rem;
  }
  .resume-container {
    grid-template-columns: 100%;
    grid-template-rows: repeat(16, minmax(5rem, auto));
    width: 90%;
    height: auto;
    padding-bottom: 0;
  }
  .resume-download-btn {
    display: none;
  }
  .resume-heading::before {
    content: "";
    flex: 1 1;
    border-style: solid;
    border-width: 3px;
    border-color: var(--clr-grey-7);
    margin: auto;
  }
  .resume-heading::before {
    margin-right: 1rem;
  }
  .resume-logo {
    width: 60%;
    margin: 0 auto;
  }
  .resume-title {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }
  .resume-summary {
    grid-row: 3 / 6;
    grid-column: 1 / 2;
  }
  .resume-skills {
    grid-row: 6 / 8;
    grid-column: 1 / 2;
    padding: 0 2rem;
  }
  .resume-experience {
    grid-row: 8 / 14;
    grid-column: 1 / 2;
  }
  .resume-education {
    grid-row: 14 / 16;
    grid-column: 1 / 2;
    padding: 0 2rem;
  }
  .resume-contact-container {
    flex-direction: row;
    justify-content: space-between;
  }
  .resume-footer {
    grid-row: 16 / 17;
  }
  .resume-footer-container {
    flex-direction: column;
    align-items: center;
    padding: 1rem 0;
    margin-top: 0;
  }
  .resume-contact-item span {
    margin-left: 0.5rem;
  }
}
@media screen and (max-width: 600px) {
  .resume-logo {
    width: 80%;
  }
  .resume-contact-container {
    flex-direction: column;
  }
  .experience-title {
    flex-direction: column;
  }
  .resume-title {
    transform: scale(0.8);
  }
}
@media screen and (max-width: 500px) {
  .resume-heading::before,
  .resume-heading::after,
  .resume-summary-heading::before,
  .resume-summary-heading::after {
    display: none;
  }
  .resume-summary,
  .resume-skills,
  .resume-experience,
  .resume-education,
  .resume-contact {
    padding: 0 1rem;
  }
}
/*-------------
  Portfolio
--------------*/
.portfolio-container {
  width: 80%;
  height: 100%;
  margin: 0 auto;
}
.portfolio-smallscreen {
  padding-bottom: 2rem;
  display: none;
}
.portfolio-carousel {
  height: 35rem;
  position: relative;
}
.portfolio-next-btn,
.portfolio-prev-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.portfolio-prev-btn {
  left: -7rem;
}
.portfolio-next-btn {
  right: -5rem;
}

.portfolio-card {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 25rem;
  height: 30rem;
  border-radius: 20px;
  background-color: var(--white-20);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(5px);
  margin: 4rem 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
}
.portfolio-card-img {
  width: 80%;
  margin-top: 3rem;
  background: rgba(255, 255, 255, 0.5);
  padding: 1rem;
  border-radius: 20px;
}

.portfolio-card p {
  font-family: var(--ff-primary);
  font-size: 1.5rem;
  color: var(--primary);
  text-transform: uppercase;
  display: flex;
}
.portfolio-card p::before,
.portfolio-card p::after {
  content: "";
  flex: 1 1;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: radial-gradient(rgba(255, 255, 255, 1), rgba(0, 0, 0, 0.2));
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
  margin: auto;
}
.portfolio-card p::after {
  margin-left: 1rem;
}
.portfolio-card p::before {
  margin-right: 1rem;
}
article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: transform 0.3s ease;
}
.portfolio-activeSlide {
  transform: translateX(0) scale(1.05);
  opacity: 1;
  z-index: 10;
}
.portfolio-activeSlide .portfolio-card {
  background-color: var(--white-20);
}

.portfolio-lastSlide {
  transform: translateX(-30%) scale(0.9);
  opacity: 0.5;
  backdrop-filter: blur(5px);
  z-index: -10;
}
.portfolio-lastSlide .portfolio-card,
.portfolio-nextSlide .portfolio-card {
  background-color: rgba(0, 0, 0, 0.2);
}
.portfolio-nextSlide {
