/*-------------
  Graphics
--------------*/
.front-spiral {
  position: absolute;
  top: 0;
  left: -7%;
  height: 100%;
}
.middle-spiral {
  position: absolute;
  top: 0;
  left: -7%;
  height: 100%;
}
.back-spiral {
  position: absolute;
  top: 0;
  left: 94%;
  height: 100%;
}
.graphics-container {
  margin: 5%;
  position: relative;
  height: 100%;
  padding-bottom: 5rem;
}
.graphics-page {
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(8px);
  position: relative;
}
.graphics-cover-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
}
.graphics-anu-logo {
  display: block;
  margin: 1rem auto;
  width: 70%;
}
.graphics-cover-item p {
  font-size: 2rem;
}
.graphics-cover-item .title::after {
  margin-left: 1rem;
}
.graphics-cover-item .title::before {
  margin-right: 1rem;
}
.graphics-item-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  font-size: 2rem;
}
.graphics-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
}
.graphics-item-container {
  display: flex;
}
.graphics-btn-container {
  margin: 2rem auto 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.graphics-btn {
  font-family: var(--ff-primary);
  padding: 0.75rem;
  margin: 0;
  text-align: center;
}
.graphics-btn-container span {
  font-family: var(--ff-primary);
  color: var(--secondary);
  margin-bottom: 1rem;
}
@media screen and (max-width: 680px) {
  .graphics-btn-container {
    margin-top: 5%;
  }
}
@media screen and (max-width: 600px) {
  .graphics-container {
    margin-top: 10%;
  }
  .graphics-btn {
    font-size: 0.75rem;
  }
}
@media screen and (max-width: 450px) {
  .graphics-btn-container {
    margin-top: 0;
  }
}
@media screen and (max-width: 500px) {
  .graphics-cover-item p {
    font-size: 1.2rem;
  }
