    font-size: 1.2rem;
  }
}
/*----
  Web
------*/
.web-container {
  width: 80%;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.web-container h2 {
  width: 100%;
}
.web-items-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(12rem, auto));
  grid-gap: 2rem;
  position: relative;
}
.web-item {
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(5px);
  padding: 0.5rem;
  cursor: pointer;
  overflow: hidden;
}
.web-item-box {
  position: relative;
}
.web-item-img {
  width: 100%;
  border-radius: 15px;
  display: block;
  transition: transform 0.3s ease;
}
.web-item-title {
  font-family: var(--ff-secondary);
  font-style: bold;
  padding-top: 0.5rem;
  color: var(--primary);
  text-align: center;
}
.web-item-view-project {
  color: var(--secondary);
  font-family: var(--ff-primary);
  font-size: 1rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
}
.web-item:hover .web-item-img {
  transform: translateY(-35px);
}
.web-back-btn {
  margin-top: 3rem;
  font-family: var(--ff-primary);
  padding: 0.75rem 1rem;
}
/*----------------
  Web Projects
----------------*/
.web-project-container {
  background-color: var(--white-20);
  box-shadow: 10px 20px 30px rgba(0, 0, 0, 0.3);
  border-top: 3px solid var(--white-40);
  border-left: 3px solid var(--white-40);
  margin: 2rem auto;
  border-radius: 2rem;
