/*----------------
  Internship
----------------*/
.internship-container {
  width: 90%;
  margin: 4rem auto;
  display: flex;
}
.internship-item-container {
  width: 80%;
  padding: 3rem;
  text-align: center;
  border: none;
  outline: none;
  background: var(--white-20);
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  border-radius: 1.5rem;
  backdrop-filter: blur(5px);
  position: relative;
}
.internship-banner {
  width: 90%;
}
.internship-tools-smallscreen {
  display: none;
}
.internship-link-container {
  display: flex;
  flex-direction: column;
}
.internship-project-container img {
  padding: 3rem 2rem;
}
.int-projects-link {
  flex: 1 1 auto;
  width: 10rem;
  height: 5rem;
  border-radius: 1.5rem;
  text-align: center;
  font-family: var(--ff-primary);
  font-size: 1.2rem;
  position: relative;
  color: var(--secondary);
  display: inline-block;
  border: none;
  outline: none;
  background: var(--white-20);
  cursor: pointer;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(5px);
  overflow: hidden;
  transition: transform 0.1s ease;
}
.internship-link-container :not(:last-child) {
  margin-bottom: 2rem;
  margin-right: 3rem;
}
.int-projects-link:hover {
  color: var(--primary);
}
.int-projects-link span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.int-projects-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    rgba(225, 161, 181, 1),
    rgba(225, 161, 181, 0.3),
    rgba(225, 161, 181, 0)
  );
  opacity: 0;
  z-index: -1;
}
.int-projects-link:hover::before {
  opacity: 1;
}
.int-projects-link-active {
  color: var(--primary);
  background: radial-gradient(
    rgba(225, 161, 181, 1),
    rgba(225, 161, 181, 0.3),
    rgba(225, 161, 181, 0)
  );
  flex: 1 1 auto;
  width: 10rem;
  height: 5rem;
  border-radius: 1.5rem;
  text-align: center;
  font-family: var(--ff-primary);
  font-size: 1.2rem;
  position: relative;
  display: inline-block;
  border: none;
  outline: none;
  background: var(--white-20);
  cursor: pointer;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.3);
  border-top: 2px solid var(--white-40);
  border-left: 2px solid var(--white-40);
  backdrop-filter: blur(5px);
  overflow: hidden;
  transition: transform 0.1s ease;
}
.int-projects-link-active span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (max-width: 900px) {
  .internship-item-container {
    width: 100%;
  }
  .internship-link-container {
    flex-direction: row;
  }
  .int-projects-link,
  .int-projects-link-active {
    width: 100%;
    padding: 0 0.5rem;
  }
  .internship-container {
    flex-direction: column;
  }
  .internship-link-container :not(:last-child) {
    margin-right: 1rem;
  }
  .internship-tools-bigscreen {
    display: none;
  }
  .internship-tools-smallscreen {
    display: block;
  }
}
@media screen and (max-width: 600px) {
  .int-projects-link span,
  .int-projects-link-active span {
    font-size: 0.9rem;
  }
  .int-projects-link-container :not(:last-child) {
    margin-right: 0.5rem;
  }
  .internship-project-container img {
    padding: 2rem 1rem;
  }
}
@media screen and (max-width: 500px) {
  .int-projects-link,
  .int-projects-link-active {
    height: 4rem;
  }
  .int-projects-link span,
  .int-projects-link-active span {
    font-size: 0.6rem;
  }
  .internship-project-container img {
    padding: 0;
  }
}
